import React from "react";
import Helmet from "react-helmet";

import Layout from '../components/layout';

import "../assets/scss/privacy.scss"; 
 
class Privacy extends React.Component {
    render() {
        return (
            <Layout>
                <div id="privacy"> 
                    <div className="privacy-header">
                        <p className="title">Privacy Policy</p> 
                    </div>

                    <div className="privacy-container banner">
                        <div className="section">
                            <h3>MADTHREAD PTE LTD Privacy Policy</h3>
                            <p>Effective Date</p>
                            <p>25 August 2018</p>
                        </div>

                        <div className="section">
                            <h3>Overview</h3>
                            <p>MadThread ( MadThread ,  we , or  us ) knows that you care how information about you is used and shared, and we appreciate your trust that we will do so carefully and sensibly. This Privacy Policy explains how we collect, protect, use and share information about you when you use MadThread.com (collectively, the  Website ), as well as any of our services, including our mobile applications widgets, plug-ins, that we created for the purposes of using the Website or our services (collectively, the  Services ), and whether you access the Website directly or through a third-party website or via one or more of the Services.</p>
                            <p>We hope this information increases your confidence in MadThread and enhances your experience with MadThread. Each time that you use the Services, you signify that you agree to be bound by the Terms and Conditions and this Privacy Policy. If you do not agree to the terms of this Privacy Policy you must discontinue using the Website and the Services.</p>
                        </div>

                        <div className="section">
                            <h3>Notice Of Information We Collect And How We Use It</h3>
                            <p>We collect two types of data about individuals: (1) technical data automatically collected from all visitors to the Website, and individuals that use the Services ( Passive Data Collection ) and (2) information that you or others voluntarily submit to us ( User-Submitted Information ).</p>
                            <p> Personal Information,  as used in this Privacy Policy, is information that allows the direct identification of an individual, such as a name or email address, and information that is directly combined with such identifying information. Our primary purpose in collecting Personal Information is to provide you with a safe, smooth, efficient, and customized experience. We only collect Personal Information about you that we consider necessary for achieving this purpose.</p>

                            <h3>Passive Data Collection</h3>
                            <p>We automatically track certain information based upon your behaviour on the Website. We use this information to do internal research on our users’ demographics, interests, and behaviour to better understand, protect and serve you and our community.</p>

                            <h3>Mobile Device Information</h3>
                            <p>When you download or use our MadThread mobile application ( App ), we receive information about your mobile device (but not the native manufacturer unique identifier for your device) for purpose of tracking unique downloads. We register your device, and may use the identifier to send you push notifications about your transactions with our service.</p>

                            <h3>Log Files</h3>
                            <p>Like most websites, we automatically receive and record certain information about our users’ behavior on our Website and when using our Services. Our servers automatically gather technical information about our Website traffic and store it in log files. This information includes Internet protocol (IP) addresses, browser type, Internet service provider (ISP), referring/exit pages, operating system, date/time stamp, and clickstream data. We aggregate this information with other pieces of information to analyze trends, to administer and improve the Services, to track users’ movements around the Website and to make inventory purchasing decisions that will best serve our customer base. Pages of the Website may also use devices that permit us to count users who have visited those pages and for other related website statistics (for example, recording the popularity of certain website content and verifying system and server integrity).</p>
                            <p>If we directly combine any information gathered through passive means with Personal Information, we treat the combined information as Personal Information under this Privacy Policy. Otherwise, we use information collected by passive means in aggregated forms.</p>

                            <h3>Clear GIFs</h3>
                            <p>We use clear gifs in our HTML-based emails to let us know which emails recipients have opened. This allows us to gauge the effectiveness of certain communications and the effectiveness of our marketing campaigns. If you would like to opt-out of these emails, you may contact us at remove@MadThread.com to request that we remove this information from our database.</p>

                            <h3>Third Party Advertisers</h3>
                            <p>Third party advertising companies may use non-personally identifiable information (e.g., click stream information, browser type, time and date, subject of advertisements clicked or scrolled over) during your visits to this Website and other web sites in order to provide advertisements about goods and services likely to be of greater interest to you. These companies typically use a cookie or third party web beacon to collect this information. These third party cookies do not collect personally identifiable information.</p>
                        </div>

                        <div className="section">
                            <h3>User-Submitted Information</h3>
                            <h3>Registration</h3>
                            <p>In order to use some features of the Website and the Services, including without limitation the rental of products from MadThread, you must first register with us and create a user name and password. During registration you are also required to give truthful contact and demographic information (including name, email address, postal code and phone number) in accordance with our Terms and Conditions. We use this information to contact you about the products and Services on our Website in which you have expressed interest.</p>

                            <h3>Subscription</h3>
                            <p>If you subscribe to our service, we request certain Personal Information from you on our order form, whether you access the Services from our App or the Website. You must provide contact information (including name, shipping address, telephone number) and financial information (including credit card number and expiration date). Also, in order to assist you, we may also require personal characteristics, such as your height, bust, and dress size.</p>
                            <p>We use this information for billing purposes and to fill your orders. If we have trouble processing an order, we will use this information to contact you.</p>
                            <p>Before your order can be processed, we will require that you review and consent to our terms and conditions.</p>

                            <h3>Surveys Or Contests</h3>
                            <p>We may provide you the opportunity to participate in surveys or contests on our site. If you participate, we will request certain Personal Information. The requested information typically includes contact information (such as name and shipping address) and demographic information.</p>
                            <p>We use this information to notify contest winners and award prizes, to monitor Website traffic, or to personalize the Website (in the case of anonymous information collected in surveys) and to send participants an email newsletter. By participating in a survey or contest you submit to that activity’s terms and conditions, including providing us with permission, if required, to share your Personal Information with s<span>p</span>onsors of that survey and/or contest.</p>
                            <p>We may use a third party service provider to conduct these surveys or contests, in which case the third party's service providers use of our users’ Personal Information will be governed by its privacy policy, which will be made available to users at the time they enroll for the survey or contest.</p>

                            <h3>Tell-A-Friend</h3>
                            <p>If you choose to use our referral service to tell a friend about our Services, we will ask you for your friend’s name and email address. We will automatically send your friend a one-time email inviting him or her to visit the Website. MadThread stores this information for the sole purpose of sending this one-time email and tracking the success of our referral program.</p>

                            <h3>Third-Party Account Credentials</h3>
                            <p>When you provide us with your third party account credentials, to such services as Facebook and Google, you understand some content and/or information in those accounts may be transmitted into your account with us. However, information transmitted to our Website from your third party account will be covered by this Privacy Policy.</p>

                            <h3>Communication From Us Or The Website</h3>
                            <p>We use your contact information to communicate with you about MadThread and our marketing partners, as described below.</p>

                            <h3>Special Offers And Updates</h3>
                            <p>We will occasionally send you information on Services, products, s<span>p</span>ecial deals, and promotions. You can take advantage of promotions by entering the promotion code provided at the time of checkout. Out of res<span>p</span>ect for your privacy, we present the option not to receive these types of communications. If you would like to opt-out of these emails, you may contact us at remove@MadThread.com to request that we remove this information from our database.</p>
                            <p>We send newsletters, usually by email. If you would like to opt-out of these emails, you may contact us at remove@MadThread.com to discontinue newsletters.</p>

                            <h3>Messages About The Service And Your Account</h3>
                            <p>We will send you strictly service-related announcements on rare occasions when it is necessary to do so. For instance, if our Services are temporarily sus<span>p</span>ended for maintenance, we might send you an email. Generally, you may not opt-out of these communications, which are not promotional in nature. If you do not wish to receive them, you have the option to deactivate your account.</p>
                            <p>Based upon the Personal Information you provide us, we will send you a welcoming email to verify your username and password. We will communicate with you in res<span>p</span>onse to your inquiries, to provide the services you request, to update you on the status of your requests, to manage your account, and to provide the best customer service possible.</p>

                            <h3>App Notifications</h3>
                            <p>If you download and use our App, and have enabled push notifications on your mobile device, we may send you alerts and notifications through push notifications, for example, to communicate shipping status for goods you rent. You can opt out of these transactional messages by changing the settings on your mobile device.</p>

                            <h3>Address Verification</h3>
                            <p>We use third party software to verify your identity and address, in order to ensure proper shipping and billing procedures for your rental.</p>
                        </div>

                        <div className="section">
                            <h3>Disclosure Of Passive Or User-Submitted Data</h3>
                            <h3>MADTHREAD Partners</h3>
                            <p>MadThread engages in marketing and promotional activities with carefully screened brands, or,  Partners,  which we think will appeal to our user base. We may make our user list available to Partners so that you may receive catalogs, product information, or promotions from our Partners. If you prefer we don’t use your information for partner promotions, please contact us at remove@MadThread.com. We also may send or permit Partner promotions via email. You may opt out of such promotional emails or Partner mailings by unsubscribing thru emailing us at remove@MadThread.com.</p>
                            <p>From time to time, Partners may also be provided with non-identifying or aggregate information indicating the demographic information of users on our Website, buying behavior, and opportunities to market to select segments of our customer base indirectly. When we share aggregate user data, we do so in a way that it cannot be linked with a user's Personal Information.</p>

                            <h3>Service Providers</h3>
                            <p>We may provide Personal Information to third parties for their use in performing internal business functions (e.g., mailing, maintenance, security, data analytics, email transmission, CRM, database management services, credit card payment processing or data hosting) on our behalf. These service providers agree to only use your Personal Information in accordance with this Privacy Policy and for no other purpose than to provide us or our Partners with the contracted-for services.</p>

                            <h3>Related Parties</h3>
                            <p>We may share some or all of your Personal Information with our affiliates (including a parent company, subsidiaries, joint venture partners or other companies that we control or that are under common control with us), in which case we will seek to require those affiliates to honor this Privacy Policy.</p>

                            <h3>As Required By Law / Certain Business Transactions</h3>
                            <p>We may disclose Personal Information if we have a good-faith belief that doing so is required by a subpoena or other judicial or administrative order or otherwise required by law or in connection with an actual or proposed corporate transaction or insolvency proceeding involving all or part of the business or asset to which the information pertains. This may include res<span>p</span>onding to legal requests from jurisdictions outside of Singapore, where we have a good faith belief that the res<span>p</span>onse is required by law in that jurisdiction.</p>

                            <h3>To Protect Rights</h3>
                            <p>Additionally, we may disclose Personal Information where we, in good faith, deem it appropriate or necessary to prevent violation of the Terms and Conditions, or our other agreements; take precautions against liability; protect the rights, property, or safety of MadThread, any individual, or the general public; maintain and protect the security and integrity of our Services or infrastructure; protect ourselves and our Services from fraudulent, abusive, or unlawful uses; investigate and defend ourselves against third-party claims or allegations; or assist government enforcement agencies.</p>

                            <h3>Co-Branded Sites</h3>
                            <p>In the event that our Website links to other websites that include our branding, this Privacy Policy does not apply to those other websites. Visitors to those websites are advised to carefully read the policies on those individual websites.</p>

                            <h3>Tracking Technologies And Tailored Advertisements / Choices</h3>
                            <p>MadThread also partners with advertising companies to place our advertising on publisher websites on the Internet. These advertising companies collect anonymous information about your visits to our Website by placing and reading cookies on the device you use to access our web site. The cookies do not collect or use Personal Information. These tracking technologies are used to analyze user behaviors in the aggregate. They are used to analyze and optimize our App or Website, measure conversion, and tailor content to interests.</p>

                            <h3>Links To Other Websites</h3>
                            <p>The Website contains links to other sites that are not owned or controlled by MadThread. Please be aware that this Privacy Policy applies only to information collected by MadThread and we are not res<span>p</span>onsible for the privacy policies and practices of such other sites. We encourage you to be aware when you leave the Website and to read the privacy policies of each and every website.</p>

                            <h3>Access To Personally Identifiable Information</h3>
                            <p>If your information changes, or if you no longer desire our Services, you may correct, update, delete it or deactivate your account by emailing our Customer Support at remove@MadThread.com</p>

                            <h3>Security</h3>
                            <p>The security of your information is important to us, including, but not limited to, the Personal Information collected via the Website. We use reasonable security measures to protect against the loss, unauthorized access, misuse, alteration and destruction of Personal Information under our control, both during the transmission and once we receive it. This includes, but is not limited to, the use of firewalls and encryption. Although we make good faith efforts to maintain the security of such Personal Information, no method of transmission over the Internet or method of electronic storage, is 100% secure and we cannot guarantee that it will remain free from unauthorized access, use, disclosure, or alteration. Further, while we work hard to ensure the integrity and security of our network and systems, we cannot guarantee that our security measures will prevent  hackers  or other unauthorized persons from illegally accessing or obtaining this information. We keep Personal Information no longer than necessary for legitimate business purpose or as required by law.</p>
                            <p>As part of securing personal data stored on our servers, we restrict access to Personal Information to MadThread employees, contractors and agents who need to know that information in order to operate, develop or improve the Services. These individuals are bound by confidentiality obligations and may be subject to discipline, including termination and criminal prosecution, if they fail to meet these obligations.</p>
                            <p>If we learn of a security breach, we may attempt to notify you electronically so that you can take appropriate protective steps. By using the Services or the Website or by providing Personal Information to us, you agree that we can communicate with you electronically regarding security, privacy, and administrative issues relating to your use of the Services.</p>
                            <p>If a security systems breach occurs, we may post a notice on our homepage (MadThread.com) or elsewhere on the Website and may send email to you at the email address you have provided to us. Depending on where you live, you may have a legal right to receive notice of a security breach in writing. This notice paragraph applies to users of Services and the Website.</p>

                            <h3>Change Of Ownership</h3>
                            <p>In the event of a change in ownership, or a merger with, acquisition by, or transfer or sale of all or a portion of our assets to, another entity, we reserve the right to transfer all of your Personal Information, including email addresses, to that entity. We will use reasonable efforts to notify registered users of any such transfer to an unaffiliated third party (by a posting on our homepage, or by email to your email address that you provided to us, as chosen by us in our discretion).</p>

                            <h3>Children</h3>
                            <p>The Children and Young Person Act protects the online privacy of children under 14 years of age. MadThread and the Website are not directed toward individuals under the age of eighteen (18), and we request that such individuals do not provide Personal Information through our Website. Additionally, we do not knowingly collect or maintain Personal Information from anyone under the age of 18, unless or except as permitted by law. If we learn that Personal Information has been collected from a user under 18 years of age on or through the Website, then we will take the appropriate steps to cause this information to be deleted. If you are the parent or legal guardian of a child under 18 who has registered on the Website or you believe has otherwise provided Personal Information to MadThread, please contact MadThread at hello@MadThread.com to have that child’s account terminated and information deleted.</p>

                            <h3>Changes In This Privacy Policy</h3>
                            <p>We may change this Privacy Policy from time to time. The most recent version of the Privacy Policy will always be posted on the Website, with the "Effective Date" posted at the top of the Privacy Policy. We may revise and update this Privacy Policy if our practices change, as technology changes, or as we add new services or change existing ones. If we make any material changes to our Privacy Policy or how we handle your personal information, or we are going to use any Personal Information in a manner that is materially different from that stated in our Privacy Policy at the time we collected such information, we will give you a reasonable opportunity to consent to the change. By using our Website or Services after the Effective Date, you are deemed to consent to our then-current Privacy Policy.</p>

                            <h3>Contact Us/Feedback</h3>
                            <p>If you have questions, concerns or feedback regarding this Privacy Policy, you can contact us at hello@MadThread.com.</p>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}

export default Privacy;